import { Map } from 'maplibre-gl';
import { useCallback, useEffect } from 'react';

/**
 * Dash array sequence for line animation.
 */
const dashArraySequence = [
  [0, 4, 3],
  [0.5, 4, 2.5],
  [1, 4, 2],
  [1.5, 4, 1.5],
  [2, 4, 1],
  [2.5, 4, 0.5],
  [3, 4, 0],
  [0, 0.5, 3, 3.5],
  [0, 1, 3, 3],
  [0, 1.5, 3, 2.5],
  [0, 2, 3, 2],
  [0, 2.5, 3, 1.5],
  [0, 3, 3, 1],
  [0, 3.5, 3, 0.5],
];

/**
 * Animate dash array for line.
 * @param map Mapbox map instance
 * @param layerId Layer id to animate
 */
function animateDashArray(map: Map, layerId: string, speed: number) {
  let step = 0;
  function animateDashArray(timestamp: number) {
    const newStep = Math.floor((timestamp / speed) % dashArraySequence.length);
    if (newStep !== step && map.getLayer(layerId)) {
      map.setPaintProperty(layerId, 'line-dasharray', dashArraySequence[step]);
      step = newStep;
    }
    requestAnimationFrame(animateDashArray);
  }
  animateDashArray(0);
}

type LineAnimationProps = {
  map: Map;
  layerId: string;
  speed?: number;
};

/**
 * Hook to animate dash array for line.
 * @param map Mapbox map instance
 * @param layerId Layer id to animate
 */
export const useLineAnimation = ({ map, layerId, speed = 50 }: LineAnimationProps) => {
  /**
   * Start line animation if layer exists.
   */
  const lineAnimation = useCallback(() => {
    if (!map) return;

    // Wait for layer to be added to map before animating
    const waitForLayer = () => {
      const layer = map.getLayer(layerId);
      if (layer) {
        if (layer.type !== 'line') {
          throw new Error(`Layer ${layerId} is not a line layer`);
        }
        animateDashArray(map, layerId, speed);
      } else {
        setTimeout(waitForLayer, 100);
      }
    };
    waitForLayer();
  }, [map, layerId, speed]);

  /**
   * Bind line animation to map load event.
   */
  useEffect(() => {
    map?.on('style.load', lineAnimation);
    return () => {
      map?.off('style.load', lineAnimation);
    };
  }, [map, lineAnimation]);

  return {};
};
